import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Explore from '@/components/Explore'
import About from '@/components/About'
import Tutorial from '@/components/Tutorial'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/monument',
      name: 'Home',
      component: Home
    },
    {
      path: '/monument/explore',
      name: 'Explore',
      component: Explore
    },
    {
      path: '/monument/about',
      name: 'About',
      component: About
    },
    {
      path: '/monument/tutorial',
      name: 'Tutorial',
      component: Tutorial
    },
  ]
})



